
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XButton from 'vux/src/components/x-button/index.vue'

import ProductDetail from './product-detail'
import ProductInfo from './product-info'
import ProductParam from './product-param'

export default {
components: {
XHeader, Tab, TabItem, Sticky, ViewBox, Search, Tabbar, TabbarItem, Swiper, Scroller, Drawer, Popup, XButton
},
directives: {
TransferDom
},
data () {
return {
selectedTab: 0,
show: false
}
},
computed: {
tabContent () {
return this.selectedTab === 0 ? ProductDetail : this.selectedTab === 1 ? ProductInfo : ProductParam
}
},
methods: {
onItemClick () {
console.log(1)
},
onTabbarItemClick1 () {
this.show = true
},
onTabbarItemClick2 () {
this.$vux.alert.show({
title: 'don\'t click me, ok'
})
},
log (info) {
console.log(info)
}
}
}



