
import ViewBox from 'vux/src/components/view-box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: 'Login',
components: {
ViewBox, XButton
},
methods: {
onHrefClick (path) {
this.$router.push({ path })
},
onRegister () {
this.$vux.alert.show({
title: '',
onHide: () => this.$router.push({ path: '/login' })
})
},
onSendCode () {
this.$vux.alert.show({
title: ''
})
}
}
}



